import React, { useEffect, useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import { useParams } from "react-router-dom";
import { Button } from "primereact/button";
import { Card } from "primereact/card";
import { Calendar } from "primereact/calendar";
import { Dropdown } from "primereact/dropdown";
import { InputNumber } from "primereact/inputnumber";
import { InputText } from "primereact/inputtext";
import { InputTextarea } from "primereact/inputtextarea";
import { Message } from "primereact/message";
import { shallowEqual } from "react-redux";
import { useAppDispatch, useAppSelector } from "../../utilities/hooks";
import { opportunityActions } from "../../store/opportunity/opportunitySlice";
import { accountActions } from "../../store/account/accountSlice";
import { campaignActions } from "../../store/campaign/campaignSlice";
import { currencyActions } from "../../store/currency/currencySlice";
import { userActions } from "../../store/user/userSlice";
import { IAppState } from "../../models/appState";
import { IOpportunity } from "../../models/opportunity";
import { v4 as uuidv4 } from "uuid";
import {
  convertDateToString,
  convertStringToDate,
  convertCurrency,
  convertToNumber,
} from "../../utilities/helpers";
import { leadSourceData } from "../../utilities/globals";
import { NumberNull } from "../../models/numberNull";

const OpportunityEdit: React.FC = () => {
  const opportunityTypeData = ["Existing Business", "New Business"];
  const salesStageData = [
    "Prospecting",
    "Qualification",
    "Needs Analysis",
    "Value Proposition",
    "Id. Decision Makers",
    "Perception Analysis",
    "Proposal/Price Quote",
    "Negotiation/Review",
    "Closed Won",
    "Closed Lost",
  ];

  const defaultItem: IOpportunity = {
    id: "",
    name: "",
    assignedUserId: "",
    assignedUserLastName: "",
    opportunityType: "",
    campaignId: "",
    leadSource: "",
    amount: null,
    amountUsDollar: null,
    currencyId: "",
    dateClosed: "",
    nextStep: "",
    salesStage: "",
    probability: null,
    accountId: "",
    accountName: "",
    description: "",
  };
  const navigateTo = "/opportunities";
  const navigate = useNavigate();
  const [subtitle, setSubtitle] = useState("Create");
  const [item, setItem] = useState(defaultItem);
  const [dirty, setDirty] = useState(false); // if form input has been changed, dirty is true
  const [valid, setValid] = useState(false); // if form input is valid, valid is true
  const [errors, setErrors] = useState({}); // indicates if fields are valid
  const params = useParams();
  const memo = useRef(true);

  const [
    opportunity,
    isFetching,
    isError,
    accounts,
    campaigns,
    currencies,
    users,
    isProfile,
    profile,
  ] = useAppSelector(
    (state: IAppState) => [
      state.opportunities.opportunity,
      state.opportunities.isFetching,
      state.opportunities.isError,
      state.accounts.accounts,
      state.campaigns.campaigns,
      state.currencies.currencies,
      state.users.users,
      state.users.isProfile,
      state.users.profile,
    ],
    shallowEqual
  );
  const dispatch = useAppDispatch();

  useEffect(() => {
    if (memo.current) {
      memo.current = false;
      dispatch(accountActions.getAccounts());
      dispatch(campaignActions.getCampaigns());
      dispatch(currencyActions.getCurrencies());
      dispatch(userActions.getUsers());
      if (params.mode === "edit") {
        setSubtitle("Edit");
        const id = params.id;
        if (id) {
          dispatch(opportunityActions.getOpportunityById(id));
        }
      }
    }
  }, [params, dispatch]);

  useEffect(() => {
    if (
      params.mode !== "edit" &&
      isProfile &&
      profile &&
      profile.currencyId &&
      item.currencyId === "" &&
      profile.currencyId !== item.currencyId
    ) {
      setItem({ ...item, currencyId: profile.currencyId });
    }
  }, [params, isProfile, profile, item]);

  useEffect(() => {
    if (
      params.mode === "edit" &&
      item.id !== opportunity?.id &&
      !isFetching &&
      !isError
    ) {
      setItem(opportunity);
    }
  }, [params, item, opportunity, isFetching, isError]);

  useEffect(() => {
    if (!dirty) {
      return;
    }

    let isValid = true;
    const filter = ["name", "accountId", "accountName", "dateClosed", "amount"];
    for (const [key, value] of Object.entries(item)) {
      if (filter.indexOf(key) < 0) {
        continue;
      }

      if (typeof value === "string") {
        if (value?.trim().length === 0) {
          isValid = false;
          break;
        }
      }

      if (key === "amount" && value === null) {
        isValid = false;
        break;
      }
    }

    setValid(isValid);
  }, [dirty, item]);

  const convertAmount = (value: any) => {
    const id = item.currencyId ? item.currencyId : "";
    const amount = convertCurrency(id, value, item.amount, currencies);

    return {
      currencyId: value,
      amount,
    };
  };

  const getAmountUsDollar = (value: any) => {
    let amountUsDollar: NumberNull = null;
    const amount = convertToNumber(value);
    if (amount) {
      let currency = currencies.find((x) => x.id === item.currencyId);
      if (currency) {
        if (currency.symbol === "$") {
          amountUsDollar = amount;
        } else {
          currency = currencies.find((x) => x.symbol === "$");
          if (currency && currency.id && item.currencyId) {
            amountUsDollar = convertCurrency(
              item.currencyId,
              currency.id,
              amount,
              currencies
            );
          }
        }
      }
    }

    return { amount, amountUsDollar };
  };

  const onChange = (e: any) => {
    setDirty(true);
    const {
      target: { name: tname, value: tvalue },
    } = e;

    if (tname === "assignedUserId") {
      const user = users.find((value) => value.id === tvalue);
      if (user) {
        setItem({
          ...item,
          [tname]: tvalue,
          assignedUserLastName: user.lastName,
        });
      }
    } else if (tname === "accountId") {
      const account = accounts.find((value) => value.id === tvalue);
      if (account) {
        setItem({ ...item, [tname]: tvalue, accountName: account.name });
      }
    } else if (tname === "dateClosed") {
      setItem({
        ...item,
        [tname]: convertDateToString(tvalue),
      });
    } else if (tname === "currencyId") {
      setItem({ ...item, ...convertAmount(tvalue) });
    } else if (tname === "amount") {
      setItem({ ...item, ...getAmountUsDollar(tvalue) });
    } else {
      setItem({ ...item, [tname]: tvalue });
    }

    if (
      tname === "name" ||
      tname === "accountId" ||
      tname === "dateClosed" ||
      tname === "amount"
    ) {
      setErrors({
        ...errors,
        [tname]: tvalue ? tvalue.toString().trim().length === 0 : true,
      });
    }
  };

  const onSaveClick = async () => {
    if (subtitle !== "Edit") {
      item.id = uuidv4();
    }

    dispatch(opportunityActions.updateOpportunity(item));
    setTimeout(() => {
      dispatch(opportunityActions.getOpportunities());
    }, 300);
    navigate(navigateTo);
  };

  const onCancelClick = async () => {
    navigate(navigateTo);
  };

  return (
    <>
      <h3 className="caption">Opportunity: {subtitle}</h3>
      <div className="grid">
        <div className="col-12">
          <span className="field-required">*</span> - Required field
        </div>
      </div>
      <Card>
        <div className="grid">
          <div className="col-12">
            <h3>Opportunity</h3>
          </div>
        </div>
        <div className="grid">
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="OpportunityName">
                  Opportunity Name <span className="field-required">*</span>
                </label>
              </div>
              <div className="col">
                <InputText
                  id="OpportunityName"
                  name="name"
                  maxLength={50}
                  value={item.name}
                  onChange={onChange}
                />
                {(errors as any)?.name && (
                  <Message
                    severity="error"
                    text="Opportunity Name is required."
                  />
                )}
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="Type">Type</label>
              </div>
              <div className="col">
                <Dropdown
                  id="Type"
                  name="opportunityType"
                  options={opportunityTypeData}
                  value={item.opportunityType}
                  onChange={onChange}
                  className="p-combobox"
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="AccountName">
                  Account Name <span className="field-required">*</span>
                </label>
              </div>
              <div className="col">
                <Dropdown
                  id="AccountName"
                  name="accountId"
                  options={accounts}
                  optionLabel="name"
                  optionValue="id"
                  value={item.accountId}
                  onChange={onChange}
                  className="p-combobox"
                />
                {(errors as any)?.accountId && (
                  <Message severity="error" text="Account Name is required." />
                )}
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="LeadSource">Lead Source</label>
              </div>
              <div className="col">
                <Dropdown
                  id="LeadSource"
                  name="leadSource"
                  options={leadSourceData}
                  value={item.leadSource}
                  onChange={onChange}
                  className="p-combobox"
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="Campaign">Campaign</label>
              </div>
              <div className="col">
                <Dropdown
                  id="Campaign"
                  name="campaignId"
                  options={campaigns}
                  optionLabel="name"
                  optionValue="id"
                  value={item.campaignId}
                  onChange={onChange}
                  className="p-combobox"
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="AssignedTo">Assigned To</label>
              </div>
              <div className="col">
                <Dropdown
                  id="AssignedTo"
                  name="assignedUserId"
                  options={users}
                  optionLabel="lastName"
                  optionValue="id"
                  value={item.assignedUserId}
                  onChange={onChange}
                  className="p-combobox"
                />
              </div>
            </div>
          </div>
        </div>
      </Card>
      <div className="grid">
        <div className="col-12">&nbsp;</div>
      </div>
      <Card>
        <div className="grid">
          <div className="col-12">
            <h3>Finance</h3>
          </div>
        </div>
        <div className="grid">
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="SalesStage">Sales Stage</label>
              </div>
              <div className="col">
                <Dropdown
                  id="SalesStage"
                  name="salesStage"
                  options={salesStageData}
                  value={item.salesStage}
                  onChange={onChange}
                  className="p-combobox"
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="CloseDate">
                  Close Date <span className="field-required">*</span>
                </label>
              </div>
              <div className="col">
                <Calendar
                  id="CloseDate"
                  name="dateClosed"
                  value={convertStringToDate(item.dateClosed)}
                  onChange={onChange}
                  showButtonBar
                  showIcon
                  className="p-combobox"
                />
                {(errors as any)?.dateClosed && (
                  <Message severity="error" text="Close Date is required." />
                )}
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="Currency">
                  Currency <span className="field-required">*</span>
                </label>
              </div>
              <div className="col">
                <Dropdown
                  id="Currency"
                  name="currencyId"
                  options={currencies}
                  optionLabel="name"
                  optionValue="id"
                  value={item.currencyId}
                  onChange={onChange}
                  className="p-combobox"
                />
                {(errors as any)?.currencyId && (
                  <Message severity="error" text="Currency is required." />
                )}
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="NextStep">Next Step</label>
              </div>
              <div className="col">
                <InputText
                  id="NextStep"
                  name="nextStep"
                  maxLength={100}
                  value={item.nextStep}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="Amount">
                  Amount <span className="field-required">*</span>
                </label>
              </div>
              <div className="col">
                <InputNumber
                  id="Amount"
                  name="amount"
                  value={item.amount}
                  onValueChange={onChange}
                />
                {(errors as any)?.amount && (
                  <Message severity="error" text="Amount is required." />
                )}
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="Probability">Probability</label>
              </div>
              <div className="col">
                <InputNumber
                  id="Probability"
                  name="probability"
                  value={item.probability}
                  onValueChange={onChange}
                />
              </div>
            </div>
          </div>
        </div>
      </Card>
      <div className="grid">
        <div className="col-12">&nbsp;</div>
      </div>
      <Card>
        <div className="grid">
          <div className="col-12">
            <h3>Description</h3>
          </div>
        </div>
        <div className="grid">
          <div className="col-12">
            <InputTextarea
              name="description"
              value={item.description}
              onChange={onChange}
              autoResize
            />
          </div>
        </div>
      </Card>
      <div className="grid">
        <div className="col-12">&nbsp;</div>
        <div className="col-12">
          <Button
            icon="pi pi-check"
            className="p-button-rounded p-button-outlined"
            tooltip="Save"
            disabled={!valid}
            onClick={onSaveClick}
          />
          &nbsp; &nbsp;
          <Button
            icon="pi pi-times"
            className="p-button-rounded p-button-outlined"
            tooltip="Back to List"
            onClick={onCancelClick}
          />
        </div>
      </div>
    </>
  );
};

export default OpportunityEdit;
